const formatDistanceLocale = {
  lessThanXSeconds: {
    one: "di 1 giy",
    other: "di {{count}} giy",
  },

  xSeconds: {
    one: "1 giy",
    other: "{{count}} giy",
  },

  halfAMinute: "na pht",

  lessThanXMinutes: {
    one: "di 1 pht",
    other: "di {{count}} pht",
  },

  xMinutes: {
    one: "1 pht",
    other: "{{count}} pht",
  },

  aboutXHours: {
    one: "khong 1 gi",
    other: "khong {{count}} gi",
  },

  xHours: {
    one: "1 gi",
    other: "{{count}} gi",
  },

  xDays: {
    one: "1 ngy",
    other: "{{count}} ngy",
  },

  aboutXWeeks: {
    one: "khong 1 tun",
    other: "khong {{count}} tun",
  },

  xWeeks: {
    one: "1 tun",
    other: "{{count}} tun",
  },

  aboutXMonths: {
    one: "khong 1 thng",
    other: "khong {{count}} thng",
  },

  xMonths: {
    one: "1 thng",
    other: "{{count}} thng",
  },

  aboutXYears: {
    one: "khong 1 nm",
    other: "khong {{count}} nm",
  },

  xYears: {
    one: "1 nm",
    other: "{{count}} nm",
  },

  overXYears: {
    one: "hn 1 nm",
    other: "hn {{count}} nm",
  },

  almostXYears: {
    one: "gn 1 nm",
    other: "gn {{count}} nm",
  },
};

export const formatDistance = (token, count, options) => {
  let result;

  const tokenValue = formatDistanceLocale[token];
  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (count === 1) {
    result = tokenValue.one;
  } else {
    result = tokenValue.other.replace("{{count}}", String(count));
  }

  if (options?.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return result + " na";
    } else {
      return result + " trc";
    }
  }

  return result;
};
